.addVideo {
    display: inline-block;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: end;
        align-items: flex-end;
    position: relative;
    margin-left: 7px;
}
.video {
    width: 100%;
    height: auto;
}
.addVideoPopover {
    margin-top: 10px;
    top: 100%;
    background: #f2f2f2;
    position: absolute;
    height: 54px;
    width: 300px;
    border-radius: 2px;
    padding: 10px;
    border: 1px solid #c2c2c2;
    -webkit-box-shadow: 0px 4px 30px 0px rgba(220, 220, 220, 1);
            box-shadow: 0px 4px 30px 0px rgba(220, 220, 220, 1);
    z-index: 1000;
}

.addVideoClosedPopover {
    display: none;
}

.addVideoButton {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-image: url("./assets/media.png");
    background-repeat: no-repeat;
    background-position: 50% 100%;
    background-color: #f2f2f2;
    border: none;
    padding: 0;
    margin: 0;
    cursor: pointer;
    width: 30px;
    height: 18px;
    margin: 0;
    opacity: 0.75;
}

.addVideoButton:focus {
    outline: 0; /* reset for :focus */
}

.addVideoButton:hover {
    opacity: 1;
}

.addVideoButton:active {
    opacity: 1;
}

.addVideoPressedButton {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-image: url("./assets/media-active.png");
    background-repeat: no-repeat;
    background-position: 50% 100%;
    background-color: #f2f2f2;
    border: none;
    padding: 0;
    margin: 0;
    cursor: pointer;
    width: 30px;
    height: 18px;
    margin: 0;
    opacity: 0.75;
}

.addVideoBottomGradient {
    width: 100%;
    height: 1em;
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    background-color: white;
    pointer-events: none;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0)), to(rgba(255, 255, 255, 1)));
    background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 100%);
    background: -o-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 100%);
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 100%);
}

.addVideoInput {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 1px solid #c2c2c2;
    cursor: text;
    padding: 4px;
    width: 78%;
    height: 30px;
    border-radius: 2px;
    margin-bottom: 1em;
    background: #fefefe;
}

.addVideoConfirmButton {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: #fff;
    border: 1px solid #c2c2c2;
    padding: 0;
    color: #888;
    margin: 0;
    border-radius: 2px;
    cursor: pointer;
    height: 30px;
    width: 18%;
    font-size: 1em;
    margin: 0;
    margin-left: 4%;
}

.addVideoConfirmButton:focus {
    outline: 0; /* reset for :focus */
}

.addVideoConfirmButton:hover {
    background: #f3f3f3;
}

.addVideoConfirmButton:active {
    background: #e6e6e6;
}


