* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
}
.editor {
  position: relative;
  margin: 93px auto 0;
  width: 686px;
  background-color: #ffffff;
}
.editor-controls {
  display: inline-block;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.editor:after {
  content: '';
  position: absolute;
  bottom: -3px;
  width: 100%;
  height: 6px;
  cursor: ns-resize;
}
.editor__header {
  background: #f2f2f2;
  height: 48px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  border: 1px solid #c2c2c2;
  border-radius: 3px 3px 0 0;
  padding: 0 11px;
}
.editor__btn {
  display: inline-block;
  margin: 4px 10px 0 9px;
  vertical-align: top;
  position: relative;
  opacity: 0.75;
  padding: 0 12px;
  font-size: 0;
  width: 25px;
  height: 22px;
}
.editor__btn--active,
.editor__btn:hover {
  color: #168ab0;
  opacity: 1;
}
.editor-btn:after {
  content: '';
  display: inline-block;
  width: 1px;
  height: 11px;
  background: #d6d6d6;
  position: absolute;
  right: -12px;
  top: 1px;
}
.editor__btn:last-child:after {
  display: none;
}
.editor__btn--bold {
  background-image: url("./images/bold.png");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  width: 10px;
  height: 13px;
}
.editor__btn--bold.editor__btn--active,
.editor__btn--bold:active {
  background-image: url("./images/bold-active.png");
}
.editor__btn--italic {
  background-image: url("./images/italic.png");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  width: 11px;
  height: 13px;
}
.editor__btn--italic.editor__btn--active,
.editor__btn--italic:active {
  background-image: url("./images/italic-active.png");
}
.editor__btn--heading {
  background-image: url("./images/heading.png");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  width: 57px;
  height: 14px;
}
.editor__btn--heading:after {
  top: 0;
}
.editor__btn--heading.editor__btn--active,
.editor__btn--heading:active {
  background-image: url("./images/heading-active.png");
}
.editor__btn--heading-rus {
  top: 1px;
  background-position: -92px -102px;
  width: 77px;
  height: 10px;
  background-size: 247px, 229px;
  background-repeat: no-repeat;
}
.editor__btn--heading-rus:after {
  top: 0;
}
.editor__btn--heading-rus.editor__btn--active,
.editor__btn--heading-rus:active {
  background-position: -92px -116px;
  width: 77px;
  height: 10px;
  background-size: 247px, 229px;
  background-repeat: no-repeat;
}
.editor__btn--ordered-list {
  background-image: url("./images/order-list.png");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  width: 17px;
  height: 14px;
}
.editor__btn--ordered-list:after {
  top: 2px;
}
.editor__btn--ordered-list.editor__btn--active,
.editor__btn--ordered-list:active {
   background-image: url("./images/order-list-active.png");
}
.editor__btn--unordered-list {
  background-image: url("./images/unorder-list.png");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 14px;

}
.editor__btn--unordered-list:after {
  top: 2px;
}
.editor__btn--unordered-list.editor__btn--active,
.editor__btn--unordered-list:active {
   background-image: url("./images/unorder-list-active.png");
}
.editor__btn--link {
  top: -2px;
  background-position: -207px -50px;
  width: 16px;
  height: 16px;
  background-size: 247px, 229px;
  background-repeat: no-repeat;
}
.editor__btn--link:after {
  top: 3px;
}
.editor__btn--link.editor__btn--active,
.editor__btn--link:active {
  background-position: 0px -176px;
  width: 16px;
  height: 16px;
  background-size: 247px, 229px;
  background-repeat: no-repeat;
}
.editor__btn--image {
  top: -2px;
  background-position: -177px -127px;
  width: 18px;
  height: 15px;
  background-size: 247px, 229px;
  background-repeat: no-repeat;
}
.editor__btn--image:after {
  top: 3px;
}
.editor__btn--image.editor__btn--active,
.editor__btn--image:active {
  background-position: -200px -89px;
  width: 18px;
  height: 15px;
  background-size: 247px, 229px;
  background-repeat: no-repeat;
}
.editor__btn--media {
  top: -2px;
  background-position: -199px -108px;
  width: 18px;
  height: 15px;
  background-size: 247px, 229px;
  background-repeat: no-repeat;
}
.editor__btn--media:after {
  top: 3px;
}
.editor__btn--media.editor__btn--active,
.editor__btn--media:active {
  background-position: -177px -108px;
  width: 18px;
  height: 15px;
  background-size: 247px, 229px;
  background-repeat: no-repeat;
}
.editor__body {
  background: #ffffff;
  min-height: 500px;
  border: 1px solid #d6d6d6;
  border-top-color: #f3f3f3;
  border-radius: 0 0 3px 3px;
}
.editor__textarea {
  border: none;
  resize: none;
  padding: 12px 7px 12px 20px;
  color: #302a2a;
  background: #ffffff;
  font-size: 13px;
}
.editor__textarea--gray {
  background: #f5f5f5;
}
.event__body a,
.editor__textarea a {
  text-decoration: none;
  color: #168ab0;
  cursor: pointer;
}
.event__body a:not(.close):not(.image-preview__link):not(.link--hover-off):hover,
.editor__textarea a:not(.close):not(.image-preview__link):not(.link--hover-off):hover,
.event__body a:not(.close):not(.image-preview__link):not(.link--hover-off):focus,
.editor__textarea a:not(.close):not(.image-preview__link):not(.link--hover-off):focus {
  border-bottom: 1px solid #9bb1cc;
}
.event__body p,
.editor__textarea p {
  margin: 0 0 8px 0;
}
.event__body h3,
.editor__textarea h3 {
  font-size: 15px;
  font-weight: bold;
  margin: 15px 0 9px;
}
.event__body ul,
.editor__textarea ul,
.event__body ol,
.editor__textarea ol {
  margin: 10px 0 9px;
  padding-left: 19px;
  line-height: 22px;
}
.event__body ol,
.editor__textarea ol {
  counter-reset: myCounter;
}
.event__body ol li,
.editor__textarea ol li {
  list-style: none;
}
.event__body ol li:before,
.editor__textarea ol li:before {
  counter-increment: myCounter;
  content: counter(myCounter) '.';
  margin: 0 3px 0 0;
}
.event__body ul li,
.editor__textarea ul li {
  list-style: none;
}
.event__body ul li:before,
.editor__textarea ul li:before {
  content: '';
  display: inline-block;
  background-position: -241px -28px;
  width: 5px;
  height: 5px;
  background-size: 247px, 229px;
  background-repeat: no-repeat;
  margin: 0 8px 0 0;
  position: relative;
  top: -2px;
}
.event__body img,
.editor__textarea img {
  border-radius: 3px;
  display: block;
  max-width: 100%;
}
.page--editor-content .editor__body {
  height: calc(100vh - 341px);
  /* minus top and bottom */
}
.editor__textarea .content-img__figure--comment img {
  border-radius: 3px 3px 0 0;
}



